      h1 Ajuste y mejora continua de la instrucción turística

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p El ajuste y mejora continua de la instrucción turística constituye un proceso sistemático de gestión de la calidad, fundamental para garantizar la excelencia en los servicios de interpretación patrimonial. Este proceso se fundamenta en el Ciclo PHVA, una metodología de mejora continua que permite la optimización constante mediante cuatro fases interrelacionadas. Para comprender cómo funciona el proceso de mejora continua, es indispensable explicar qué es y cómo funciona el Ciclo PHVA: 
      p Es un método sistemático para la mejora continua, que significa Planificar – Hacer – Verificar – Actuar. Esta herramienta permite gestionar cambios de manera organizada y basada en evidencias, asegurando que cada modificación en el servicio turístico responda a necesidades reales y esté debidamente fundamentada.

    .tarjeta(titulo="Ejemplo - 1")(data-aos="fade-left")
