            .tarjeta.color-acento-botones.p-2(x="4.9%" y="56.5%" numero="")
              .h5.mb-2 Definición:
              p Fase de diseño y preparación, donde se establecen los objetivos de mejora basados en datos recolectados y análisis previos. 

            .tarjeta.color-acento-botones.p-3(x="4.9%" y="66.6%" numero="")
