            .tarjeta.color-acento-botones.p-3(x="52.2%" y="66.6%" numero="")
              .h5.mb-2 Actividades concretas:
              .p - Medir resultados utilizando los instrumentos diseñados en la fase de planificación.
              .p - Comparar datos antes y después de las mejoras.
              .p - Analizar el impacto real en la experiencia del visitante. 
              .p - Identificar efectos no previstos, tanto positivos como negativos.
              .p - Validar resultados con fuentes diversas.
            
            .tarjeta.color-acento-botones.p-3(x="52.2%" y="76.5%" numero="")
              .h5.mb-2 Ejemplo:
              p “Aplicamos cuestionarios a 150 visitantes después de la demostración de tejido, comparándolos con 150 que no participaron. Los resultados mostraron que la comprensión cultural aumentó del 35% al 82%.”
            
            .tarjeta.color-acento-botones.p-3(x="77.2%" y="56.5%" numero="")
