              .p - Comunicar decisiones al equipo.
              .p - Actualizar manuales, guiones y procedimientos.
              
            
            .tarjeta.color-acento-botones.p-3(x="77.2%" y="76.5%" numero="")
              .h5.mb-2 Ejemplo aplicado:
