    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      h3 Bitácoras de mejora y gestión de datos
      p La efectividad de este proceso depende de contar con un sistema robusto de recolección y análisis de datos, que transforme la retroalimentación cualitativa y cuantitativa en insumos accionables. La implementación de bitácoras de mejora —procesos documentales que realizan el seguimiento de actividades concretas en el marco del ciclo PHVA— permite registrar sistemáticamente los ajustes realizados, su justificación y su impacto, creando un historial evolutivo del servicio. Este registro sirve como evidencia para procesos de certificación y como material de formación para nuevos guías.
      p Respecto a la información recopilada, es necesario comprender cómo se clasifican, cuál es su utilidad y un ejemplo de aplicación:

